
.eleven_content{
  background: url(../img/eleven_backgorund.png);
  background-size:cover;
  background-repeat: no-repeat;
  margin-top: 166px;
}

.contact_page{
  max-width: $max-width;
  margin: 0 auto;
  text-align: center;
}

.contact_page h2{
  padding-top: 100px;
}

.container_to_input{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 20px;
  padding-top: 140px;
}

.container_to_input input{
    border:none;
    width: 100%;
    height: 60px;
    padding-left: 20px;
    border: 1px solid $color_for_grey_h2;
}

textarea{
  margin-top: 15px;
  padding-left: 20px;
  padding-top: 20px;
  border: none;
  width: 100%;
  height: 510px;
  padding-bottom: 25px;
  resize: none;
  border: 1px solid $color_for_grey_h2;
}

.message .form-main-button{
  text-align: left;
}

.message .form-main-button button{
  width: 145px;
  font: 17px $font-article;
  font-weight: bolder;
  margin-bottom: 141px;
  background-color: $color_for_yellow;
}

.message .form-main-button button:hover{
  background-color: $color_for_h1;
  color: $color_for_yellow;
}
